.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
}

.modal-content {
    background-color: #fefefe;
    margin: 15% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
    max-width: 500px;
}

.modal-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.chat-form {
    display: flex;
    flex-direction: column;
}

.create-button {
    margin-top: 5px;
    width: min-content;
}

.close-button {
    align-self: flex-start;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: none;
    border: none;
    cursor: pointer;
}

.close-button .material-symbols-outlined {
    color: #aaa;
}

.close-button:hover,
.close-button:focus {
    transition: background-color 0.5s;
    background-color: #EBEBEB;
}